import React, { Component } from "react";
import { connect } from "react-redux";
import loading from "../../assets/icons/loading/LOADING-MLS.gif";
import "./Login.css";
import logoLogistical from "../../assets/icons/IconLogistical.png";
import { GET_USER } from "apiServices/action";

class Logins extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formValidation: {
        username: true,
        password: true,
      },
      validationEmail: true,
      errorMessage: "",
      isLoad: false,
      forgotPassword: false,
      policy: false,
      forgotSuccess: false,
      emailValue: "",
    };
  }

  validateForm = async (e) => {
    e.preventDefault();
    let errorMessage = "";
    const { forgotPassword } = this.state;
    const { dispatch, history } = this.props;
    if (!forgotPassword) {
      const username = e.target.username.value;
      const password = e.target.password.value;
      if (
        username === "dev@onebyone.io" &&
        password === "OBO12121212"
      ) {
        this.setState({
          isLoad: false,
          errorMessage,
          formValidation: { username: true, password: true },
        });

        const data = {
          email: "dev@onebyone.io",
          warehouseName: "Kebon Jeruk Warehouse",
          warehouseUuid: "c78a5512-8e78-11e9-9220-42010ace9102",
          warehouseCode: "JKT01",
          warehouseTypeName: "Storage",
          inventoryRole: "operator",
          sessionId: "session123",
          modules: [
            "stock_take",
            "stock_transfer",
            "waste",
            "outbound",
            "additional_inbound",
            "stock_movement",
            "repacking",
          ],
          role: "operator",
        };

        dispatch({
          type: GET_USER,
          user: data,
        });

        history.push("/");
      } else {
        errorMessage = "Please enter your registered account";
        this.setState({
          isLoad: false,
          errorMessage,
          errorMessage,
          formValidation: { username: false, password: false },
        });
      }
    } else {
    }
  };

  changeFormHanlder = () => {
    const { forgotPassword } = this.state;
    this.setState({
      forgotPassword: !forgotPassword,
      errorMessage: "",
      formValidation: { username: true, password: true },
      validationEmail: true,
    });
  };

  changePolicyHandler = () => {
    this.setState({ policy: true, forgotPassword: false });
  };

  changeTermHandler = () => {
    this.setState({ policy: true, forgotPassword: true });
  };

  exitPolicyHandler = () => {
    this.setState({
      policy: false,
      forgotPassword: false,
      forgotSuccess: false,
    });
  };

  redirectPageHandler = () => {
    this.window.location.replace(window.location.origin);
  };

  hideErrorMessageHandler = (errorMessage) => {
    this.setState({ isLoad: false, errorMessage, validationEmail: true });
  };

  onChangeEmail = (e) => {
    this.setState({ emailValue: e.target.value });
  };

  loginForm(errorMessage, formValidation) {
    const { forgotPassword, isLoad } = this.state;
    return (
      <form
        method="post"
        className={`mt-3 ${forgotPassword ? "form-hidden" : "form-show"}`}
        onSubmit={this.validateForm}
      >
        <span>Email</span>
        <input
          autoComplete="off"
          className={`form-control inputLogin ${
            errorMessage === "" ? "" : "border-red"
          } ${errorMessage === "Password is required" ? "none" : ""}`}
          style={
            formValidation.username ? {} : { borderColor: "#f44336 !important" }
          }
          type="text"
          name="username"
          placeholder="Enter your User ID or Email here"
        />
        <br />
        <span>Password</span>
        <input
          autoComplete="off"
          className={`form-control inputLogin ${
            errorMessage === "" ? "" : " border-red"
          } ${errorMessage === "Username is required" ? "none" : ""}`}
          type="password"
          name="password"
          placeholder="Enter your password here"
        />
        <div className={`error ${errorMessage ? "alertFadeIn" : ""}`}>
          {errorMessage && (
            <div>
              <span className="iconU-i" />
              {errorMessage}
            </div>
          )}
        </div>
        <div className="row">
          <div className="pl-3 pr-0" style={{ width: "30%" }}>
            <button
              type="submit"
              className="btn btn-login col-12"
              onClick={() => this.setState({ errorMessage: "" })}
            >
              {isLoad ? (
                <img
                  src={loading}
                  alt=""
                  className="mt-min-5"
                  width="45"
                  height="45"
                />
              ) : (
                "LOGIN"
              )}
            </button>
          </div>
          <div className="col-7 mt-3">
            <span
              className="form-login-change"
              onClick={() => this.changeFormHanlder()}
              aria-hidden="true"
            >
              FORGOT PASSWORD
            </span>
          </div>
        </div>
      </form>
    );
  }

  forgotPasswordForm(errorMessage) {
    const { forgotPassword, forgotSuccess, emailValue, isLoad } = this.state;

    const errorEmail = errorMessage
      .toString()
      .split("Please check your email again.");
    const issueEmail = errorMessage.toString().slice(-30);

    return (
      <form
        className={`mt-3 ${forgotPassword ? "form-show" : "form-hidden"}`}
        onSubmit={this.validateForm}
      >
        {forgotSuccess ? (
          <div className="col-10 px-0" style={{ marginTop: "17px" }}>
            <div className="forgotPassMsg" style={{ color: "#4775ff" }}>
              New Password has been sent to your email
            </div>
            <div className="forgotPassMsg">{emailValue}</div>
          </div>
        ) : (
          <div>
            <input
              autoComplete="off"
              onChange={this.onChangeEmail}
              className={`form-control inputLogin ${
                errorMessage === "" ? "" : "border-red"
              }`}
              type="text"
              name="email"
              placeholder="Enter your email address here"
            />
            <span className="email-message">
              Enter your email address to reset password
            </span>
          </div>
        )}
        <div className={`error ${errorMessage ? "alertFadeIn" : ""}`}>
          {errorMessage && (
            <div>
              <span className="iconU-i" />
              {errorEmail ? (
                <>
                  {errorEmail}
                  <br />
                  {issueEmail}
                </>
              ) : (
                errorMessage
              )}
            </div>
          )}
        </div>
        <div className="row forgot-row">
          {forgotSuccess ? (
            <div className="pr-0 pl-3 white-space" style={{ width: "30%" }}>
              <button
                onClick={() => this.exitPolicyHandler()}
                type="button"
                className="btn btn-primary btn-login col-12"
              >
                {isLoad ? (
                  <img
                    src={loading}
                    alt=""
                    className="mt-min-5"
                    width="45"
                    height="45"
                  />
                ) : (
                  "BACK"
                )}
              </button>
            </div>
          ) : (
            <div className="pr-0 pl-3 white-space" style={{ width: "30%" }}>
              <button type="submit" className="btn   btn-login col-12">
                {isLoad ? (
                  <img
                    src={loading}
                    alt=""
                    className="mt-min-5"
                    width="45"
                    height="45"
                  />
                ) : (
                  "SEND"
                )}
              </button>
            </div>
          )}
          {forgotSuccess ? null : (
            <div className="col-sm-8 mt-3">
              <span
                className="form-login-change"
                onClick={() => this.changeFormHanlder()}
                aria-hidden="true"
              >
                LOGIN PAGE
              </span>
            </div>
          )}
        </div>
      </form>
    );
  }

  render() {
    // const { loginImage } = this.props.store;
    const { errorMessage, formValidation, forgotPassword, policy } = this.state;
    let formComponent = this.loginForm(errorMessage, formValidation);
    if (forgotPassword && !policy)
      formComponent = this.forgotPasswordForm(errorMessage, formValidation);
    if (!forgotPassword && !policy)
      formComponent = this.loginForm(errorMessage, formValidation);
    if (!forgotPassword && policy) formComponent = this.privacyAndPolicy();
    if (forgotPassword && policy) formComponent = this.termAndCondition();

    return (
      <div className="login">
        <div className="container-fluid">
          <div className="card col-md-3 col-12 col-sm-12 box-login offset-md-1 p-0">
            <div className="card-body login-card">
              {/* logo */}
              <div>
                <img
                  src={logoLogistical}
                  className="login-logo img-fluid mx-auto"
                  alt="Logo"
                  style={{ maxHeight: "60px", marginTop: "20px" }}
                />
              </div>
              {/* <img src={loginImage} className="logo mb-2" alt="mlslogo" /> */}
              {formComponent}
              {/* <div className="links mt-3">
                <span onClick={() => this.changePolicyHandler()} className="term-and-condition">
                  Privacy and Policy
                </span>
                <span> &nbsp; | &nbsp; </span>
                <span onClick={() => this.changeTermHandler()} className="term-and-condition">
                  Terms and Conditions
                </span>
              </div> */}
            </div>
          </div>
          <div className="offset-md-1 mt-5">
            <a
              className="text-white "
              target="blank"
              href="https://www.microlistics.com.au/"
            >
              Â© Logistical Warehouse {new Date().getFullYear()}
            </a>
          </div>
          <div className="offset-md-1 mb-4">
            <span className="text-logo"></span>
          </div>
          <br />
        </div>
      </div>
    );
  }
}
const mapStateToProps = (store) => ({ store });
const mapDispatchToProps = (dispatch) => ({ dispatch });
export default connect(mapStateToProps, mapDispatchToProps)(Logins);
