export const DROPDOWN_ACTION = 'DROPDOWN_ACTION'

export const setDropdown = ({ name, i, dispatch, isOpen, _nav = [] }) => {
    const updateNav = _nav.map(n => {
        if (n.name === name) {
            n.isOpen = !isOpen
        }
        else {
            n.isOpen = false
        }
        return n
    })
    dispatch({ type: DROPDOWN_ACTION, _nav: updateNav })

}

export function roleChecking(userRole, data) {
    if (userRole === 'operator') return data.role !== 'admin'
    else return data
}