import moment from 'moment'

export const dateFull = ({ date, noTime, addDay, withDay }) => {
    let days = date
    if (addDay) days = moment(days).add(1, 'day')
    const hour = moment(days ? days : moment()).format('HH:mm')
    const day = moment(days ? days : moment()).format('dddd')
    let numDay = moment(days ? days : moment()).format('DD')
    const month = moment(days ? days : moment()).format('MMMM')
    const year = moment(days ? days : moment()).format('YYYY')
    const dateFullReturn = `${noTime ? '' : `${hour} - `} ${withDay ? `${day}, ` : ''} ${numDay} ${month} ${year}`
    return dateFullReturn
}

export const dateDMY = ({ date, datePicker }) => {
    let newDate;
    let startDate = date?.startDate
    let endDate = date?.endDate
    if (startDate && datePicker) {
        newDate = { startDate: moment(startDate).format('DD-MMM-YYYY'), endDate: moment(endDate).format('DD-MMM-YYYY') }
    }
    if (datePicker && !date?.startDate) newDate = moment(date).format("DD-MMM-YYYY")
    if (date && !datePicker) {
        newDate = moment(date).format("DD-MM-YYYY")
    }
    return newDate
}

export const isFloat = (value) => {
    if (value) {
        const check = value.toString().split("")
        if (check.includes('.')) {
            const values = value.toString().split('.')
            if (parseInt(values[1]) === 0) return values[0]
            return parseFloat(value).toFixed(2)
        }
        return parseInt(value)
    }
    else return 0
}

export const sortByInboundDate = ({ moduleEntry, moduleName, activeSku, dispatch, sortBy }) => {
    let sorted = { ...moduleEntry }
    sorted.jobPost.list[activeSku].poList.sort((a, b) => sortBy ? b.inboundDate - a.inboundDate : a.inboundDate - b.inboundDate)
    dispatch({ type: 'STATUS', [`${moduleName}EntryData`]: sorted })
}

export const filterByDate = ({ moduleEntry, moduleName, activeSku, dispatch, date }) => {
    let hidden = { ...moduleEntry }
    hidden.jobPost.list[activeSku].poList.map(po => {
        const inbnd = moment(po.inboundDate).format('MM-DD-YYYY')
        if (date) {
            if (date?.startDate) {
                const { startDate, endDate } = date

                const sd = moment(startDate).format('MM-DD-YYYY')
                const nd = moment(endDate).format('MM-DD-YYYY')
                if (inbnd < sd || inbnd > nd) po.isHidden = true
                if (inbnd >= sd && inbnd <= nd) po.isHidden = false
            }
            if (!date?.startDate) {
                const d = moment(date).format('MM-DD-YYYY')
                if (d && inbnd !== d) po.isHidden = true
                if (d && inbnd === d) po.isHidden = false
            }
        }
        // if(!date?.startDate) po.isHidden = false
        if (!date) po.isHidden = false
        return po
    })
    dispatch({ type: 'STATUS', [`${moduleName}EntryData`]: hidden })
}

export const throwErrorStatus = (e) => {
    const cancel = e?.code === 'ECONNABORTED' ? 408 : e.code
    const response = e?.response?.status
    let msg = e?.message?.split(' ')
    const servererror = msg[0] == 'timeout' ? 400 : 500
    const statusCode = cancel ? cancel : response ? response : servererror
    const errorMessage = e?.response?.data?.message
    const statusMessage = errorMessage ? errorMessage : e?.message
    const statusReturn = { code: statusCode, message: statusMessage  }
    return statusReturn
}



export const isLessThanInitWeight = (module) => {
    if (module) {
        let isTrue = []
        let isTrueView = false
        module.jobPost.list.map(ls => {
            ls.poList.map(po => {
                let qty = 0
                let isExist = po.itemList.filter(item => Number(item.qty) > Number(po.initialWeight))
                if (isExist.length > 0) isTrue.push(true)
                po.itemList.map(item => {
                    qty += Number(item.qty)
                    if (Number(qty) > Number(po.initialWeight)) isTrue.push(true)
                })
            })
        })
        if (isTrue.length){
            isTrueView = true
        }
        else{
            isTrueView = false
        }
        return isTrueView
    }
}

export const regexTest = (e) => {
    const value = e.target.value
    let rg = /^\d{0,8}(\.\d{0,1})?$/
    if (!rg.test(value)) e.preventDefault()
}

// Single view method

const modifyWasteCondition = (singleViewData) => {
    const newData = Object.assign({}, JSON.parse(JSON.stringify(singleViewData)))
    const itemList = newData.jobPost.list[0].poList[0].itemList.map(item => {
        if (item?.ripeness?.value === 'waste' || item?.ripeness?.value === 'semi_waste') {
            item.codeGrade = item.ripeness
            item.ripeness = null
            return item
        }
        else return item
    })
    newData.jobPost.list[0].poList[0].itemList = itemList
    return newData
}

export const deleteSentSKU = async ({ summary, resetSummary, name, moduleEntry, moduleName, dispatch, setIsSuccess, saveJobSheet, setLocalEntryData, setCreatePo, moveAll, singleViewData }) => {
    const data = moveAll ? moduleEntry : modifyWasteCondition(singleViewData)
    const isSent = await saveJobSheet({ name, moduleEntry: data, moduleName, dispatch, setIsSuccess })
    if (isSent) {
        const newSummary = summary?.map(sku => {
            sku.checked = false
            return sku
        })
        resetSummary(newSummary)
        if (typeof (setCreatePo) === 'function') setCreatePo({ show: false, skuNumber: null })
    }
}

export const onEnter = ({ e, idx, onClick }) => {
    if (e.keyCode === 13) {
        onClick()
        const card = document.getElementById(`sku_item_${idx}_${0}`)
        if (card) card.focus()
    }
}