const changeState = (state = initialState, { type, data, ...rest }) => {
  switch (type) {
    case "set":
      return { ...state, ...rest };
    case "EXPAND_SIDEBAR":
      return { ...state, ...rest };
    case "UPDATE_WEIGHT_ENTRY":
      return { ...state, ...rest };
    case "STATUS":
      return { ...state, ...rest };
    case DROPDOWN_ACTION:
      return { ...state, ...rest };
    case RIPENESS_LEVEL:
      return { ...state, ...rest };
    case GRADE:
      return { ...state, ...rest };
    case OUTBOUND_TYPE:
      return { ...state, ...rest };
    case GET_STOCK_TAKE_ACTION:
      return { ...state, ...rest };
    case GET_UNFINISHED_ENTRIES:
      return { ...state, ...rest };
    case WAREHOUSE_AREA:
      return { ...state, ...rest };
    case GET_USER:
      return { ...state, ...rest };
    case USER_LOGOUT:
      return initialState;
    case IS_LOADING:
      return { ...state, ...rest };
    case ADD_SKU:
      return { ...state, ...rest }
    case STOK_TAKE_UPLOAD_JSON:
      return { ...state, ...rest };
    case CREATE_JOB_SHEET:
      return { ...state, ...rest }
    case SET_AREA:
      return { ...state, ...rest }
    case VALIDATE_PO_SKU_INFO_ID:
      return { ...state, ...rest };
    case 'DATA_WAREHOUSE':
      return { ...state, warehouseData: data };
    case 'SET_ZERO_QUANTITY':
      return { ...state, zeroQuantity: data }
    case 'SET_MOVEMENT_TARGET_REPACK':
      return { ...state, stockMovementTargetRepack: data }
    default:
      return state;
  }
};

const persistConfig = {
  key: "sbroot",
  storage,
  whitelist: [
    "stockTakeEntryData",
    "outboundEntryData",
    "stockMovementEntryData",
    "stockTransferEntryData",
    "wasteEntryData",
    "repackEntryData",
    "user",
    "zeroQuantity",
    "stockMovementTargetRepack",
    "targetRepack",
    "categoryList",
    "ripenessLevel",
    "version"],
};

const persistedReducer = persistReducer(persistConfig, changeState);
export let store = createStore(persistedReducer);
export let persistor = persistStore(store);
